<template>
	<view class="">
		<view class="invite-call" v-if="callType === 2">
			<camera class="local-video" v-if="remoteUsers.length === 1" :device-position="pusher.frontCamera" />
			<view class="invite-calling">
				<view class="invite-calling-header" v-if="remoteUsers.length === 1">
					<view class="invite-calling-header-left">
						<image src="../../static/swtich-camera.png" :data-device="pusher.frontCamera"
							@click.stop="toggleSwitchCamera" />
					</view>
					<view class="invite-calling-header-right">
						<view class="invite-calling-header-message">
							<label class="tips">{{remoteUsers[0].nick || remoteUsers[0].userID}}</label>
							<text class="tips-subtitle" v-if="!isSponsor">邀请你视频通话</text>
							<text class="tips-subtitle" wx:else>等待对方接受</text>
						</view>
						<image class="avatar" :src="remoteUsers[0].avatar || '../../static/default_avatar.png'"
							:id="remoteUsers[0].userID" @error="handleErrorImage" />
					</view>
				</view>
				<view class="invite-calling-header invite-calling-list" wx:else>
					<view class="invite-calling-item" v-for="item in remoteUsers" :key="userID">
						<image class="avatar" :src="item.avatar || '../../static/default_avatar.png'" :id="item.userID"
							@error="handleErrorImage" />
						<view class="invite-calling-item-message">
							<label class="tips">{{item.nick || item.userID}}</label>
							<text class="tips-subtitle" v-if="!isSponsor">邀请你视频通话</text>
							<text class="tips-subtitle" wx:else>等待对方接受</text>
						</view>
					</view>
				</view>
				<view class="footer">
					<view class="btn-operate" v-if="isSponsor">
						<view class="btn-operate-item call-switch" @click.stop="switchAudioCall">
							<text>切换到语音通话</text>
							<view class="call-operate">
								<image src="../../static/trans.png" />
							</view>
						</view>
					</view>
					<view class="btn-operate" v-if="isSponsor">
						<view class="btn-operate-item">
							<view class="call-operate" style="background-color: red" @click.stop="hangup">
								<image src="../../static/hangup.png" />
							</view>
							<text>挂断</text>
						</view>
					</view>
					<view class="btn-operate" v-if="!isSponsor">
						<view class="btn-operate-item">
							<view class="call-operate" style="background-color: red" @click.stop="reject">
								<image src="../../static/hangup.png" />
							</view>
							<text>挂断</text>
						</view>
						<view class="btn-operate-item">
							<view class="call-operate" style="background-color: #07c160" @click.stop="accept">
								<image src="../../static/hangup.png" style="transform: rotate(-135deg); " />
							</view>
							<text>接听</text>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="incoming-call audio-call" v-if="callType === 1">
			<view class="invite-calling-single" v-if="remoteUsers.length === 1">
				<image class="avatar" :src="remoteUsers[0].avatar || './static/default_avatar.png'"
					:id="remoteUsers[0].userID" @error="handleErrorImage" />
				<view class="tips">{{remoteUsers[0].nick || remoteUsers[0].userID}}</view>
				<view v-if="isSponsor && callType === 1" class="tips-subtitle">{{'等待对方接受'}}</view>
			</view>
			<view class="invite-calling-header invite-calling-list" wx:else>
				<view class="invite-calling-item" v-for="item in remoteUsers" :key="userID">
					<image class="avatar" :src="item.avatar || '../../static/default_avatar.png'" :id="item.userID"
						@error="handleErrorImage" />
					<view class="invite-calling-item-message">
						<label class="tips">{{item.nick || item.userID}}</label>
						<text class="tips-subtitle" v-if="!isSponsor">邀请你视频通话</text>
						<text class="tips-subtitle" wx:else>等待对方接受</text>
					</view>
				</view>
			</view>
			<view class="footer">
				<!-- <view class="btn-operate">
	            <view class="btn-operate-item call-switch">
	                <text>切换到视频通话</text>
	                <view class="call-operate">
	                    <image src="../../static/trans.png" />
	                </view>
	            </view>
	        </view> -->
				<view v-if="!isSponsor && callType === 1" class="btn-operate">
					<view class="call-operate" style="background-color: red" catch:tap="reject">
						<image src="../../static/hangup.png" />
					</view>
					<view class="call-operate" style="background-color: #07c160" catch:tap="accept">
						<image src="../../static/hangup.png" style="transform: rotate(-135deg); " />
					</view>
				</view>
				<view v-if="isSponsor && callType === 1" class="btn-operate">
					<view class="call-operate" style="background-color: red" catch:tap="hangup">
						<image src="../../static/hangup.png" />
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		props: {
			isSponsor: {
				type: Boolean,
				default: false,
			},
			pusher: {
				type: Object,
			},
			callType: {
				type: Number,
			},
			remoteUsers: {
				type: Array,
			},
		},
		methods: {
			accept(event) {
				const data = {
					name: 'accept',
					event,
				};
				this.$emit('callingEvent', data);
			},
			hangup(event) {
				const data = {
					name: 'hangup',
					event,
				};
				this.$emit('callingEvent', data);
			},
			reject(event) {
				const data = {
					name: 'reject',
					event,
				};
				this.$emit('callingEvent', data);
			},
			handleErrorImage(e) {
				const {
					id
				} = e.target;
				const remoteUsers = this.remoteUsers.map((item) => {
					if (item.userID === id) {
						item.avatar = '../../static/default_avatar.png';
					}
					return item;
				});
				this.setData({
					remoteUsers,
				});
			},
			toggleSwitchCamera(event) {
				const data = {
					name: 'toggleSwitchCamera',
					event,
				};
				this.$emit('callingEvent', data);
			},
			switchAudioCall(event) {
				const data = {
					name: 'switchAudioCall',
					event,
				};
				this.$emit('callingEvent', data);
			},
		},
	}
</script>

<style>
	@import "./index.css";
</style>
